import React from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import './Admin.css';

function Admin() {
  async function AddCalc() {
    const json = document.getElementById('json').value
    const login = document.getElementById('login').value
    const pass = document.getElementById('pass').value

    const loginApi = 'http://127.0.0.1:9001/login'
    let jwt
    const loginJson = {
      login: login,
      password: pass
    }

    await fetch(loginApi, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(loginJson)
    })
      .then((result) => result.json())
      .then((result) => {
        if (Object.hasOwn(result, 'token')) {
          jwt = result.token
        } else {
          document.getElementById('message').innerText = result.message
        }
      })

    if (jwt === null) {
      return
    }

    const api = 'http://127.0.0.1:9001/calculator/add'
    const obj = JSON.parse(json)
    const data = {
      token: jwt,
      calculator: obj
    }

    await fetch(api, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(data)
    })
      .then((result) => result.json())
      .then((result) => {
        document.getElementById('message').innerText = result.message
      })
  }

  const exampleJson = {
    "nameCalc": "Test",
    "numberFields": [
      {
        "fieldName": "Field 1",
        "field": "a"
      },
      {
        "fieldName": "Field 2",
        "field": "b"
      },
      {
        "fieldName": "Field 3",
        "field": "c"
      }
    ],
    "formula": "a + b + c"
  }


  return (
    <>
      <Header />
      <div className='Admin'>
        <div className='content'>
          <p> :</p>
          <p>
                JSON.   :
          </p>
          <pre>
            {JSON.stringify(exampleJson, null, 2)}
          </pre>
          <p>nameCalc -   </p>
          <p>
            numberFields -    (JSON- ).
                  2  - field  fieldName.
            field -       math.js.
            fieldName -    .
          </p>
          <p>formula -     math.js</p>
          <textarea id="json" placeholder=" JSON-" />
          <input id="login" type="text" placeholder="   " />
          <input id="pass" type="password" placeholder="   " />
          <button id="create" onClick={() => AddCalc()}></button>
          <p id='message'></p>
        </div>
      </div>
      <Footer />
    </>
  );
}

export default Admin;